.studtabs{
    width: 100%;
}
.studtabs>.MuiTabs-root{
    width: 100% ;
    overflow: inherit;
    background-color: #fff ;
    border-radius: 10px ;
    padding: 15px 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px ;
}
.studtabs>.MuiTabs-root>.MuiTabs-scroller>.MuiTabs-flexContainer{
    width: 100% !important;
    display: flex !important;
    justify-content: space-between !important;
}
.MuiTabScrollButton-root {
    width: 20px !important;
}

